<template>
  <div id="user-edit-tab-info">

    <!-- Content Row -->
    <div class="vx-row">
      <div class="vx-col md:w-1/2 w-full">
        <div class="w-full mt-4">
          <label class="vs-input--label">{{ $t('batch.year') }}</label>
          <datepicker v-model="dataLocal.year" :minimumView="'year'" :maximumView="'year'" v-validate="'required'" name="year" :language="languages[language]" format="yyyy"></datepicker>
          <span class="text-danger text-sm"  v-show="errors.has('year')">{{ errors.first('year') }}</span>
        </div>

        <div class="w-full mt-4">
          <label class="vs-input--label">{{ this.$t('batch.status') }}</label>
          <v-select v-model="dataLocal.status" :clearable="false" :options="statusOptions" v-validate="'required'" name="status" :dir="$vs.rtl ? 'rtl' : 'ltr'" />
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('status')">{{ errors.first('status') }}</span>

        <div class="w-full mt-4">
          <label class="vs-input--label">{{ $t('batch.dateStart') }}</label>
          <datepicker v-model="dataLocal.dateStart" v-validate="'required'" name="dateStart" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('dateStart')">{{ errors.first('dateStart') }}</span>

        <div class="w-full mt-4">
          <label class="vs-input--label">{{ $t('batch.dateEnd') }}</label>
          <datepicker v-model="dataLocal.dateEnd" v-validate="'required'" name="dateEnd" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('dateEnd')">{{ errors.first('dateEnd') }}</span>
      </div>

      <div class="vx-col md:w-1/2 w-full">
        <div class="w-full mt-4">
          <label class="vs-input--label">{{ $t('batch.datePayment') }}</label>
          <datepicker v-model="dataLocal.datePayment" v-validate="'required'" name="datePayment" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('datePayment')">{{ errors.first('datePayment') }}</span>

        <div class="w-full mt-4">
          <label class="vs-input--label">{{ $t('batch.datePayment2') }}</label>
          <datepicker v-model="dataLocal.datePayment2" v-validate="'required'" name="datePayment2" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('datePayment2')">{{ errors.first('datePayment2') }}</span>

        <div class="w-full mt-4">
          <label class="vs-input--label">{{ $t('batch.dateDelivery') }}</label>
          <datepicker v-model="dataLocal.dateDelivery" v-validate="'required'" name="dateDelivery" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('dateDelivery')">{{ errors.first('dateDelivery') }}</span>
      </div>
    </div>

    <!-- Save & Reset Button -->
    <div class="vx-row">
      <div class="vx-col w-full">
        <div class="mt-8 flex flex-wrap items-center justify-end">
          <vs-button class="ml-auto mt-2" @click="save_changes" :disabled="!validateForm">{{ $t('button.save') }}</vs-button>
          <vs-button class="ml-4 mt-2" type="border" color="warning" @click="reset_data">{{ $t('button.reset') }}</vs-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import vSelect from 'vue-select'
import Datepicker from 'vuejs-datepicker';
import * as lang from 'vuejs-datepicker/src/locale';

export default {
  components: {
    vSelect,
    Datepicker
  },
  data() {
    return {
      language: "pt-BR",
      languages: lang,
      dataLocal: {},
      statusOptions: [
        { label: this.$t('active'),  value: 1 },
        { label: this.$t('deactivated'),  value: 0 },
      ],
    }
  },
  computed: {
    validateForm() {
      return !this.errors.any()
    }
  },
  methods: {
    save_changes() {
      this.$validator.validateAll()

      if(!this.validateForm) return
      this.$vs.loading()

      this.$store.dispatch("batchManagement/addBatch", this.dataLocal)
      .then(res => {
        this.batch_data = res.data.attributes
        this.setAlert({
          type: 'success',
          message: 'alert.create.success'
        })
        this.$router.push({ name: 'admin-batch-list' })
      })
      .catch(err => {
        console.error(err)
      })
      .then(() => {
        this.$vs.loading.close()
      })

    },
    reset_data() {
      this.dataLocal = {}
    }
  },
}
</script>
